<template>
  <div class="menu">
    <v-speed-dial
      v-model="settingsMenu"
      :top="true"
      :right="true"
      direction="bottom"
      transition="slide-y-transition"
    >
      <template v-slot:activator>
        <v-btn
          v-model="settingsMenu"
          :color="
            settings.difficulty
              ? settings.difficulty === 2
                ? 'deep-purple'
                : 'purple'
              : 'blue'
          "
          dark
          fab
        >
          <div
            class="menu_lang"
            :class="['menu_lang menu_lang-' + i18n.lang]"
          ></div>
          <v-icon v-if="settingsMenu"> mdi-close </v-icon>
          <v-icon v-else> mdi-cog </v-icon>
        </v-btn>
      </template>
      <v-btn
        fab
        dark
        small
        color="blue"
        :class="{ 'v-btn--menu--selected': settings.difficulty === 0 }"
        @click="setDifficulty(0)"
      >
        <v-icon>mdi-baby-bottle</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="purple"
        :class="{ 'v-btn--menu--selected': settings.difficulty === 1 }"
        @click="setDifficulty(1)"
      >
        <v-icon>mdi-chess-king</v-icon>
      </v-btn>
      <v-btn
        fab
        dark
        small
        color="deep-purple"
        :class="{ 'v-btn--menu--selected': settings.difficulty === 2 }"
        @click="setDifficulty(2)"
      >
        <v-icon>mdi-skull</v-icon>
      </v-btn>
    </v-speed-dial>
    <div
      :class="[
        { 'v-speed-dial--is-active': settingsMenu },
        'menu_placeholder v-speed-dial v-speed-dial--top v-speed-dial--right v-speed-dial--direction-left',
      ]"
    >
      <div class="v-speed-dial__list">
        <v-btn
          fab
          dark
          small
          color="blue"
          :class="{ 'v-btn--menu--selected': i18n.lang === 'ja' }"
          @click="changeLang('ja')"
          >JP</v-btn
        >
        <v-btn
          fab
          dark
          small
          color="green"
          :class="{ 'v-btn--menu--selected': i18n.lang === 'vi' }"
          @click="changeLang('vi')"
          >VI</v-btn
        >
        <v-btn
          fab
          dark
          small
          color="red"
          :class="{ 'v-btn--menu--selected': i18n.lang === 'en' }"
          @click="changeLang('en')"
          >EN</v-btn
        >
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState } from 'vuex'
import CommonService from '@/services/common.service'

export default {
  name: 'Game',
  computed: mapState({
    settings: (state) => state.settings.settings,
    i18n: (state) => state.i18n
  }),
  data: () => ({
    settingsMenu: false,
    commonService: new CommonService()
  }),
  methods: {
    setDifficulty (val) {
      this.changeDifficulty(val)
    },
    changeLang (val) {
      this.commonService.changeLang(val)
    },
    ...mapActions('settings', ['changeDifficulty'])
  }
}
</script>

<style lang="scss">
.menu {
  .v-speed-dial {
    position: absolute;
  }
  &_placeholder {
    pointer-events: none;
    height: 56px;
    margin-right: 56px;
    &_btn {
      pointer-events: none;
      width: 56px;
      height: 56px;
    }
    .v-speed-dial__list {
      opacity: 0;
      transition: opacity 300ms 25ms cubic-bezier(0.25, 0.8, 0.5, 1),
        transform 300ms cubic-bezier(0.25, 0.8, 0.5, 1);
      transform: translateX(15px);
      .v-btn {
        transition: transform cubic-bezier(0.25, 0.8, 0.5, 1);
        &:nth-child(1) {
          transition-delay: 100ms;
          transition-duration: 200ms;
          transform: translateX(1px);
        }
        &:nth-child(2) {
          transition-delay: 100ms;
          transition-duration: 100ms;
          transform: translateX(0.5px);
        }
      }
    }
    &.v-speed-dial--is-active {
      pointer-events: all;
      .v-speed-dial__list {
        opacity: 1;
        transform: translateX(0);
        transition: opacity 250ms 25ms cubic-bezier(0.25, 0.8, 0.5, 1),
          transform 300ms cubic-bezier(0.25, 0.8, 0.5, 1);
      }
      .v-btn {
        transition: transform cubic-bezier(0.25, 0.8, 0.5, 1);
        &:nth-child(1) {
          transition-delay: 100ms;
          transition-duration: 200ms;
          transform: translateX(0);
        }
        &:nth-child(2) {
          transition-delay: 200ms;
          transition-duration: 100ms;
          transform: translateX(0);
        }
      }
    }
  }
  .v-btn--menu--selected {
    border: thin solid #fff;
    border-color: #fff !important;
  }
}
</style>
